import moment, { now } from "moment";

export const dateTimeParser = (
  custdate,
  format = "MM/DD/YYYY h:mm A",
  timezone = null
) => {
  const mydate = custdate
    ? moment.utc(custdate).format(format)
    : moment().format(format);
  return mydate;
};

