import { DownOutlined, RightOutlined } from "@ant-design/icons";
import { useMutation } from "@apollo/client";
import { Badge, Button, message, Spin } from "antd";
import React, { useEffect } from "react";
import Linkify from "react-linkify";
import {
  CREATE_CHAT_WIDGET_LEAD,
  UPDATE_WEB_CHAT_INFO,
} from "./graphql/mutation";
import { PhoneWrap } from "./PhoneWrap";
import { dateTimeParser } from "./utils/date";

export const Content = (props) => {
  let render = true;
  let lastScrolled = 0;
  const [createWebChatWidgetLead, { loading }] = useMutation(
    CREATE_CHAT_WIDGET_LEAD,
    {
      fetchPolicy: "network-only",
    }
  );
  const [updateWebChatLeadInfo] = useMutation(UPDATE_WEB_CHAT_INFO, {
    fetchPolicy: "network-only",
  });
  function userAgentMutation(res) {
    updateWebChatLeadInfo({
      variables: {
        browserType: props.userAgentDetails.browserAgent,
        ipAddress: props.userAgentDetails.ipAddress,
        timezone: props.userAgentDetails.timezone,
        leadId: res?.data?.createWebChatWidgetLead?.lead?.id,
        webChatWidgetUid: props.userAgentDetails.webChatWidgetId,
      },
    });
  }
  const handlePreDefinedMessages = (
    item,
    callApi = true,
    socketMessage = true
  ) => {
    if (!props?.lead?.id && callApi) {
      createWebChatWidgetLead({
        variables: {
          companyId: props.data.location.companyId,
          locationId: props.data.locationId,
        },
      }).then((res) => {
        if (res.data.createWebChatWidgetLead.statusCode === 200) {
          props?.Socket?.emit("receive_webchat_message", {
            message: item.message,
            lead_id: res?.data?.createWebChatWidgetLead?.lead?.id,
          });
          userAgentMutation();
          props?.setMessageList((prev) => [
            ...prev,
            {
              message: item?.message,
              time: dateTimeParser(),
              type: "success",
            },
          ]);
          const element = document.getElementById("message");
          element.scrollTop = element.scrollHeight + 1000;
          props.setactionStarted(true);
          props.setlead(res.data.createWebChatWidgetLead.lead);
        } else {
          message.error(res.data.createWebChatWidgetLead.message);
        }
      });
    } else {
      if (socketMessage) {
        props?.Socket?.emit("receive_webchat_message", {
          message: item.message,
          lead_id: props?.lead?.id,
        });
      }
      // Socket messages

      props?.setMessageList((prev) => [
        ...prev,
        {
          message: item?.message,
          time: dateTimeParser(),
        },
      ]);
      props.messenger.current.scrollIntoView({ behavior: "smooth" });
      props.setactionStarted(true);
    }
  };
  useEffect(() => {
    if (props?.showInitialText && render) {
      handlePreDefinedMessages(
        { message: props?.data?.initialText },
        false,
        false
      );
      render = false;
    }
  }, []);
  useEffect(() => {
    // const element = document.getElementById("message");
    // if (element.scrollTop > lastScrolled) {
    //   if (element) {
    //     element.scrollTop = element.scrollHeight;
    //   }
    //   if (
    //     element.scrollHeight - Math.floor(element.scrollTop) ===
    //     element.clientHeight + 1
    //   ) {
    //     props.setscroll("down");
    //   }
    // } else {
    //   props.setscroll("up");
    // }
    // lastScrolled = element.scrollTop <= 0 ? 0 : element.scrollTop;
    if (props.scroll !== "up") {
      const element = document.getElementById("message");
      if (element) {
        element.scrollTop = element.scrollHeight;
      }
    }
  }, [props.messageList]);
  console.log(props.scroll);
  return (
    <div
      className={
        props?.actionStarted && props?.phoneNumber !== ""
          ? "message-div-small"
          : props.flow === "1" &&
            props?.actionStarted &&
            props?.phoneNumber === ""
          ? "message-div-without-input"
          : "message-div"
      }
      onScroll={(e) => {
        const scrollposition = e.currentTarget.scrollTop;
        if (scrollposition > lastScrolled) {
          if (
            e.currentTarget.scrollHeight -
              Math.floor(e.currentTarget.scrollTop) ===
            e.currentTarget.clientHeight + 1
          ) {
            props.setscroll("down");
          }
        } else {
          var div = document.getElementById("message");
          var hasVerticalScrollbar = div.scrollHeight > div.clientHeight;
          if (hasVerticalScrollbar) {
            props.setscroll("up");
          }
        }
        lastScrolled = scrollposition <= 0 ? 0 : scrollposition;
      }}
      ref={props.messenger}
      id="message"
    >
      {props?.actionStarted ? (
        props &&
        props.messageList &&
        props.messageList.length > 0 &&
        props?.messageList?.map((item, i) => {
          return (
            <div key={i}>
              <Linkify>
                <p
                  className={
                    props?.showInitialText
                      ? i === 0
                        ? "message-recieved"
                        : "message-sent"
                      : item.direction === "FROM_USER"
                      ? "message-recieved"
                      : "message-sent"
                  }
                  style={{ backgroundColor: item.type === "error" && "red" }}
                >
                  {item?.message}
                </p>
              </Linkify>
              <p
                className={
                  props?.showInitialText
                    ? i === 0
                      ? "time-recieved"
                      : "time-sent"
                    : item.direction === "FROM_USER"
                    ? "time-recieved"
                    : "time-sent"
                }
                style={{ fontSize: item.direction === "FROM_USER" && "12px" }}
              >
                {item.direction === "FROM_USER" ? item.user : "You"}
                {" messaged on"} {item?.time}
              </p>
            </div>
          );
        })
      ) : (
        <>
          <Spin spinning={loading} tip="Sending Message...">
            <div className="predefined-questions">
              Choose from a Question Below:
              {props &&
                props?.data &&
                props?.data?.webChatWidgetQuestion &&
                props?.data?.webChatWidgetQuestion.length > 0 &&
                props?.data?.webChatWidgetQuestion?.map((item, i) => (
                  <div
                    key={i}
                    onClick={() => {
                      if (props.flow === "1" && props.phoneNumber === "") {
                        props.setactionStarted(true);
                        props.setinitialMessage(item.question);
                      } else {
                        handlePreDefinedMessages({
                          message: item?.question,
                        });
                      }
                    }}
                    className="predefined-questions-child"
                  >
                    <p> {item?.question}</p>{" "}
                    <RightOutlined style={{ marginTop: "10px" }} />
                  </div>
                ))}
            </div>
          </Spin>
        </>
      )}
      {props.actionStarted && props.scroll === "up" && (
         <Badge dot={props.dots}>
        <Button
          type="primary"
          className="scroll-bottom"
          style={{ position: "fixed", top: "60vh", right: "11vh" }}
          onClick={() => {
            const element = document.getElementById("message");
            if (element) {
              element.scrollTop = element.scrollHeight;
            }
            props.setscroll(false)
            props.setdots(false)
          }}
        >
          <DownOutlined />
        </Button>
       </Badge>
      )}
      {props.actionStarted &&
        props.phoneNumber === "" &&
        props.flow === "1" && (
          <PhoneWrap
            messenger={props.messenger}
            message={props.initialMessage}
            handlePreDefinedMessages={handlePreDefinedMessages}
            props={props}
            showMessage={true}
          ></PhoneWrap>
        )}
      {props.actionStarted &&
        props.phoneForm &&
        props.phoneNumber === "" &&
        props.flow !== "1" && (
          <PhoneWrap
            messenger={props.messenger}
            message={props.initialMessage}
            handlePreDefinedMessages={handlePreDefinedMessages}
            props={props}
            showMessage={false}
          ></PhoneWrap>
        )}
    </div>
  );
};
