import { gql } from "@apollo/client";

export const SAVE_PHONE = gql`
  mutation savePhone($companyId: Int!, $locationId: Int!, $phone: String!) {
    savePhone(companyId: $companyId, locationId: $locationId, phone: $phone) {
      statusCode
      message
    }
  }
`;

export const CREATE_CHAT_WIDGET_LEAD = gql`
  mutation createWebChatWidgetLead(
    $companyId: Int!
    $firstName: String
    $fullName: String
    $lastName: String
    $leadSourceType: String
    $locationId: Int!
    $phone: String
    $textConsent: Boolean
    $textConsentDate: DateTime
  ) {
    createWebChatWidgetLead(
      companyId: $companyId
      firstName: $firstName
      fullName: $fullName
      lastName: $lastName
      leadSourceType: $leadSourceType
      locationId: $locationId
      phone: $phone
      textConsent: $textConsent
      textConsentDate: $textConsentDate
    ) {
      statusCode
      message
      lead {
        id
        fullName
        firstName
        lastName
      }
    }
  }
`;
export const UPDATE_CHAT_WIDGET_LEAD = gql`
  mutation updateWebChatWidgetLead(
    $firstName: String
    $fullName: String
    $lastName: String
    $leadId: Int!
    $phone: String
    $textConsent: Boolean
    $textConsentDate: DateTime
  ) {
    updateWebChatWidgetLead(
      firstName: $firstName
      fullName: $fullName
      lastName: $lastName
      leadId: $leadId
      phone: $phone
      textConsent: $textConsent
      textConsentDate: $textConsentDate
    ) {
      statusCode
      message
      lead {
        id
        fullName
        firstName
        lastName
      }
    }
  }
`;

export const CREATE_WEB_CHAT_MESSAGE_URL = gql`
  mutation createWebChatMessageUrl($leadId: Int) {
    createWebChatMessageUrl(leadId: $leadId) {
      statusCode
      message
      url
    }
  }
`;

export const UPDATE_WEB_CHAT_INFO = gql`
  mutation updateWebChatLeadInfo(
    $browserType: String
    $ipAddress: String
    $leadId: Int
    $pageLink: String
    $timezone: String
    $webChatWidgetUid: Int
  ) {
    updateWebChatLeadInfo(
      browserType: $browserType
      ipAddress: $ipAddress
      leadId: $leadId
      pageLink: $pageLink
      timezone: $timezone
      webChatWidgetUid: $webChatWidgetUid
    ) {
      statusCode
      message
    }
  }
`;
