import { gql } from "@apollo/client";

export const GET_UNIQUE_ID_DETAILS = gql`
  query getWebChatWidget($widgetUid: String!) {
    getWebChatWidget(widgetUid: $widgetUid) {
      id
      widgetUid
      locationId
      initialText
      isPhoneRequired
      switchToSmsInd
      presetQuestionInd
      isActive
      webChatWidgetQuestion {
        id
        question
      }
      location {
        companyId
        title
        company {
          name
        }
      }
    }
  }
`;

export const createWebChatWidgetMessage = gql`
  query createWebChatWidgetMessage(
    $campaignId: Int
    $campaignTemplateId: Int
    $channelId: Int
    $content: String!
    $direction: String
    $isConsent: Boolean
    $leadId: Int!
    $leadOpportunityId: Int
    $reviewLink: String
    $systemUserId: Int
    $toPhone: String!
    $userId: Int!
  ) {
    createWebChatWidgetMessage(
      campaignId: $campaignId
      campaignTemplateId: $campaignTemplateId
      channelId: $channelId
      content: $content
      direction: $direction
      isConsent: $isConsent
      leadId: $leadId
      leadOpportunityId: $leadOpportunityId
      reviewLink: $reviewLink
      systemUserId: $systemUserId
      toPhone: $toPhone
      userId: $userId
    ) {
      statusCode
      message
      leadMessage
    }
  }
`;

