import React from "react";
import ReactDOM from "react-dom/client";
import { ApolloClient, InMemoryCache, ApolloProvider } from "@apollo/client";
import "antd/dist/antd.css";
import App from "./App";
const widget = document.createElement("div");
widget.id = "chatbot-widget";
widget.style.position = "relative";
widget.style.right = "0px";
widget.style.bottom = "0px";
document.body.appendChild(widget);

const getId = document.getElementById("business-id");
const client = new ApolloClient({
  uri: process.env.REACT_APP_GRAPHQL_URL || "http://10.0.99.25:5000/chat_api",
  // uri: "https://9e59-115-246-24-52.ngrok.io/chat_api",
  cache: new InMemoryCache(),
});

const root = ReactDOM.createRoot(document.getElementById("chatbot-widget"));
root.render(
  <ApolloProvider client={client}>
    <React.StrictMode>
      <App id={getId.textContent.replaceAll('"', "")} />
    </React.StrictMode>
  </ApolloProvider>
);
