import { CloudDownloadOutlined, LeftOutlined } from "@ant-design/icons";
import { Tooltip } from "antd";
import React from "react";
export const Heading = (props) => {
  return (
    <div className="heading">
      <div style={{ display: "flex" }}>
        {" "}
        {props?.actionStarted && props?.showarrow && (
          <LeftOutlined
            style={{ marginRight: "7px", marginTop: "11px", cursor: "pointer" }}
            onClick={() => {
              // props.setMessageList([]);
              props?.setactionStarted((prev) => !prev);
              // props.setPhoneNumber("");

              // props.setPhoneValue("");
            }}
          />
        )}
        <div className="heading-div">
          {" "}
          <p>
            <span style={{ fontSize: "16px" }}>
              {" "}
              <span style={{ fontSize: "16px" }}>
                {" "}
                {props?.data?.location?.company?.name}
              </span>{" "}
              <br />
              <span style={{ fontSize: "13px" }}>
                {" "}
                {props?.data?.location?.title}
              </span>{" "}
              <br />
            </span>
          </p>
          {props.actionStarted && (
            <Tooltip title="Download your Messages">
              <CloudDownloadOutlined
                onClick={props?.DownloadMessages}
                style={{
                  marginTop: "10px",
                  marginRight: "10px",
                  cursor: "pointer",
                  fontSize: "large",
                }}
              />
            </Tooltip>
          )}
        </div>
      </div>
    </div>
  );
};
