import { CloseOutlined, MessageOutlined } from "@ant-design/icons";
import { Button } from "antd";
import React, { useState } from "react";
import "./style.css";
import { WidgetContent } from "./widgetContent";

export default function App(props) {
  const [widgetVisible, setwidgetVisible] = useState(false);
  const [popoverData, setpopoverData] = useState({
    visible: false,
    message: "",
  });
  return (
    <div style={{ height: "100%" }}>
      <div
        style={{
          position: "fixed",
          bottom: 0,
          marginBottom: "30px",
          right: 0,
          marginRight: "50px",
        }}
      >
        <div
          className="widgetContainer"
          style={{
            display: `${widgetVisible ? "block" : "none"}`,
            maxWidth: "350px",
            minWidth: "350px",
            marginLeft: "auto",
            marginRight: "16px",
            marginBottom: "8px",
            borderRadius: "15px",
            // border: "1px solid",
            borderBottomRightRadius: "0px",
            zIndex: 9999,
            backgroundColor: "white",
            boxShadow: "rgba(100, 100, 111, 0.2) 0px 7px 29px 0px",
          }}
        >
          <WidgetContent
            setpopoverData={setpopoverData}
            widgetVisible={widgetVisible}
            setwidgetVisible={setwidgetVisible}
            id={props.id}
          />
        </div>
        <div style={{ width: "100%", textAlign: "right" }}>
          {" "}
          <div
            onClick={() => {
              setwidgetVisible(true);
              setpopoverData({
                visible: false,
                message: "",
              });
            }}
            style={{
              display: `${popoverData.visible ? "block" : "none"}`,
              maxWidth: "350px",
              marginLeft: "auto",
              marginRight: "16px",
              marginBottom: "8px",
              borderRadius: "15px",
              // border: "1px solid",
              borderBottomRightRadius: "0px",
              zIndex: 9999,
              backgroundColor: "#8e0b61",
              boxShadow: "rgba(100, 100, 111, 0.2) 0px 7px 29px 0px",
              padding: "15px",
              color: "white",
              fontWeight: "bold",
              cursor: "pointer",
            }}
          >
            {popoverData.message}
          </div>
          <Button
            size="large"
            type="primary"
            style={{
              borderRadius: "30px",
              borderTopRightRadius: "5px",
            }}
            onClick={() => {
              setpopoverData({ visible: false, message: "" });
              setwidgetVisible((prev) => !prev);
            }}
          >
            {widgetVisible ? <CloseOutlined /> : <MessageOutlined />}
          </Button>
        </div>
        {/* </Popover> */}
      </div>
      <style jsx>
        {`
          .widgetContainer ::-webkit-scrollbar {
            width: 5px;
          }

          /* Track */
          .widgetContainer ::-webkit-scrollbar-track {
            box-shadow: inset 0 0 5px grey;
            border-radius: 10px;
          }

          /* Handle */
          .widgetContainer ::-webkit-scrollbar-thumb {
            background: #8e0b618f;
            border-radius: 10px;
          }

          /* Handle on hover */
          .widgetContainer ::-webkit-scrollbar-thumb:hover {
            background: rgb(78, 78, 226);
          }
        `}
      </style>
    </div>
  );
}
