/* eslint-disable react-hooks/exhaustive-deps */
import { useQuery } from "@apollo/client/react";
import { Spin } from "antd";
import React, { useEffect, useRef, useState } from "react";
import { io } from "socket.io-client";
import { Content } from "./content";
import { Footer } from "./footer";
import { GET_UNIQUE_ID_DETAILS } from "./graphql/query";
import { Heading } from "./heading";
import { SendMessage } from "./sendMessage";
import { dateTimeParser } from "./utils/date";
import { playSound } from "./utils/sound";

export const WidgetContent = (props) => {
  let lastScrolled = 0;
  const [message, setMessage] = useState("");
  const [initialMessage, setinitialMessage] = useState("");
  const messenger = useRef(null);
  const [dots, setdots] = useState(false);
  const [scroll, setscroll] = useState("");
  const [messageList, setMessageList] = useState([]);
  const [lead, setlead] = useState({});
  let socket_api = process.env.REACT_APP_SOCKET_API || "http://localhost:5000/webchat";
  const [actionStarted, setactionStarted] = useState(false);
  const [phoneNumber, setPhoneNumber] = useState("");
  const [Socket, setSocket] = useState(null);
  const [PhoneValue, setPhoneValue] = useState("");
  const [phoneForm, setphoneForm] = useState(false);
  const [Download, setDownload] = useState(false);
  const [userAgentDetails, setUserAgentDetails] = useState({
    ipAddress: "",
    browserAgent: "",
    timeZone: "",
  });
  const [flow, setflow] = useState(null);
  const [widgetData, setwidgetData] = useState();
  // window.location.href
  const fnBrowserDetect = () => {
    let userAgent = navigator.userAgent;
    let browserName;

    if (userAgent.match(/chrome|chromium|crios/i)) {
      browserName = "chrome";
    } else if (userAgent.match(/firefox|fxios/i)) {
      browserName = "firefox";
    } else if (userAgent.match(/safari/i)) {
      browserName = "safari";
    } else if (userAgent.match(/opr\//i)) {
      browserName = "opera";
    } else if (userAgent.match(/edg/i)) {
      browserName = "edge";
    } else {
      browserName = "No browser detection";
    }

    return browserName;
  };
  useEffect(() => {
    fetch("http://ip-api.com/json").then((res) => {
      res.text().then((data) => {
        const obj = JSON.parse(data);
        setUserAgentDetails({
          browserAgent: fnBrowserDetect(),
          ipAddress: obj.query,
          timeZone: obj.timezone,
          webChatWidgetId: props?.id?.replace(/\s+/g, " ")?.trim(),
        });
      });
    });
  }, []);
  let isSocket = false;
  const getDifferedState = async (setState) => {
    let differedState;
    await new Promise((resolve) =>
      setState((data) => {
        differedState = data;
        resolve();
        return data;
      })
    );
    return differedState;
  };
  useEffect(() => {
    if (!isSocket) {
      const url = process.env.REACT_APP_SOCKET_API || "http://localhost:5000";
      const socket = io(socket_api);
      if (socket) {
        socket.on("connect", () => {
          setSocket(socket);
        });
        socket.on("disconnect", () => {});

        socket?.on("receive_user_message", async (arg) => {
          const widgetVisible = await getDifferedState(props.setwidgetVisible);
          const widgetData = await getDifferedState(setwidgetData);
          const scroll = await getDifferedState(setscroll);
          setMessageList((prev) => [
            ...prev,
            {
              message: arg.content,
              time: dateTimeParser(arg.date_sent),
              user: arg.user.full_name,
              type: "success",
              direction: "FROM_USER",
            },
          ]);
          if (scroll === "top") {
            playSound(
              "https://www.mediacollege.com/downloads/sound-effects/beep/beep-03.wav"
            );
          }
          setdots(true);
          if (!widgetVisible) {
            props.setpopoverData({
              visible: true,
              message: `Recieved new message from ${arg?.user?.full_name}`,
            });
            playSound(
              "https://www.mediacollege.com/downloads/sound-effects/beep/beep-03.wav"
            );
          }
          socket.emit("send_status", arg);
        });
      }
      isSocket = true;
    }
  }, []);
  const { data, loading, error } = useQuery(GET_UNIQUE_ID_DETAILS, {
    fetchPolicy: "network-only",
    variables: {
      widgetUid: props?.id?.replace(/\s+/g, " ")?.trim(),
    },
    onCompleted: (res) => {
      if (res?.getWebChatWidget) {
        setwidgetData({ ...res?.getWebChatWidget });
        if (
          (res?.getWebChatWidget?.isPhoneRequired &&
            res?.getWebChatWidget?.switchToSmsInd) ||
          (res?.getWebChatWidget?.isPhoneRequired &&
            !res?.getWebChatWidget?.switchToSmsInd)
        ) {
          setflow("1");
        } else if (
          !res?.getWebChatWidget?.isPhoneRequired &&
          res?.getWebChatWidget?.switchToSmsInd
        ) {
          setflow("3");
        } else if (
          !res?.getWebChatWidget?.isPhoneRequired &&
          !res?.getWebChatWidget?.switchToSmsInd
        ) {
          setflow("2");
        }
        if (!res?.getWebChatWidget?.presetQuestionInd) {
          setactionStarted(true);
        }
      } else if (res.getWebChatWidget === null) {
        const element = document.getElementById("business-id");
        const div = document.getElementById("chatbot-widget");
        element.remove();
        div.remove();
      }
    },
  });
  useEffect(() => {
    if (error) {
      const element = document.getElementById("business-id");
      const div = document.getElementById("chatbot-widget");
      element.remove();
      div.remove();
    }
  }, [error]);
  const DownloadMessages = () => {
    if (phoneNumber === "") {
      setphoneForm(true);
      setDownload(true);
      const element = document.getElementById("message");
      element.scrollTop = element.scrollHeight + 1000;
    } else {
      alert("request your download URL sent successfully");
    }
  };
  return (
    !loading && (
      <div className="mainContainer">
        <Spin className="Spinner" tip="Gathering Details..." spinning={loading}>
          <Heading
            actionStarted={actionStarted}
            setactionStarted={setactionStarted}
            setMessageList={setMessageList}
            setPhoneNumber={setPhoneNumber}
            DownloadMessages={DownloadMessages}
            setPhoneValue={setPhoneValue}
            data={widgetData}
            showarrow={data?.getWebChatWidget?.presetQuestionInd}
          />
          <Content
            messageList={messageList}
            Socket={Socket}
            actionStarted={actionStarted}
            setactionStarted={setactionStarted}
            setMessageList={setMessageList}
            initialMessage={initialMessage}
            setinitialMessage={setinitialMessage}
            phoneNumber={phoneNumber}
            setPhoneNumber={setPhoneNumber}
            lead={lead}
            setlead={setlead}
            flow={flow}
            messenger={messenger}
            phoneForm={phoneForm}
            setphoneForm={setphoneForm}
            data={widgetData}
            Download={Download}
            apiData={data?.getUniqueIdDetails}
            showInitialText={!data?.getWebChatWidget?.presetQuestionInd}
            userAgentDetails={userAgentDetails}
            scroll={scroll}
            setscroll={setscroll}
            dots={dots}
            setdots={setdots}
          />
          {flow === "1" &&
            ((actionStarted && phoneNumber !== "") || !actionStarted) && (
              <SendMessage
                message={message}
                lead={lead}
                setlead={setlead}
                setMessage={setMessage}
                messageList={messageList}
                setMessageList={setMessageList}
                setactionStarted={setactionStarted}
                actionStarted={actionStarted}
                phoneNumber={phoneNumber}
                setPhoneNumber={setPhoneNumber}
                PhoneValue={PhoneValue}
                setPhoneValue={setPhoneValue}
                data={widgetData}
                flow={flow}
                messenger={messenger}
                Socket={Socket}
                Download={Download}
                userAgentDetails={userAgentDetails}
              />
            )}
          {flow !== "1" && (
            <SendMessage
              lead={lead}
              setlead={setlead}
              message={message}
              setMessage={setMessage}
              messageList={messageList}
              setMessageList={setMessageList}
              setactionStarted={setactionStarted}
              actionStarted={actionStarted}
              phoneNumber={phoneNumber}
              setPhoneNumber={setPhoneNumber}
              PhoneValue={PhoneValue}
              setPhoneValue={setPhoneValue}
              flow={flow}
              setphoneForm={setphoneForm}
              data={widgetData}
              phoneForm={phoneForm}
              Socket={Socket}
              Download={Download}
              userAgentDetails={userAgentDetails}
              messenger={messenger}
            />
          )}
          <Footer
            DownloadMessages={DownloadMessages}
            actionStarted={actionStarted}
          />
          <style jsx>
            {`
              .mainContainer ::-webkit-scrollbar {
                width: 10px;
              }

              /* Track */
              .mainContainer ::-webkit-scrollbar-track {
                box-shadow: inset 0 0 5px grey;
                border-radius: 10px;
              }

              /* Handle */
              .mainContainer ::-webkit-scrollbar-thumb {
                background: #8e0b618f;
                border-radius: 10px;
              }

              /* Handle on hover */
              .mainContainer ::-webkit-scrollbar-thumb:hover {
                background: #b30000;
              }
            `}
          </style>
        </Spin>
      </div>
    )
  );
};
