import { SendOutlined } from "@ant-design/icons";
import { useMutation } from "@apollo/client";
import { Button, Input, message } from "antd";
import React from "react";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";
import {
  CREATE_CHAT_WIDGET_LEAD,
  UPDATE_WEB_CHAT_INFO,
} from "./graphql/mutation";
import { dateTimeParser } from "./utils/date";
export const SendMessage = (props) => {
  const { TextArea } = Input;
  const [createWebChatWidgetLead] = useMutation(CREATE_CHAT_WIDGET_LEAD, {
    fetchPolicy: "network-only",
  });
  const [updateWebChatLeadInfo] = useMutation(UPDATE_WEB_CHAT_INFO, {
    fetchPolicy: "network-only",
  });
  function userAgentMutation(res) {
    updateWebChatLeadInfo({
      variables: {
        browserType: props.userAgentDetails.browserAgent,
        ipAddress: props.userAgentDetails.ipAddress,
        timezone: props.userAgentDetails.timezone,
        leadId: res?.data?.createWebChatWidgetLead?.lead?.id,
        webChatWidgetId: props.userAgentDetails.webChatWidgetId,
      },
    });
  }
  const sendMessage = (value) => {

    if (!props?.lead?.id) {
      if (value !== "") {
        createWebChatWidgetLead({
          variables: {
            companyId: props.data.location.companyId,
            locationId: props.data.locationId,
          },
        }).then((res) => {
          if (res.data.createWebChatWidgetLead.statusCode === 200) {
            props.Socket.emit("receive_webchat_message", {
              message: value,
              lead_id: res?.data?.createWebChatWidgetLead?.lead?.id,
            });

            props?.setMessageList((prev) => [
              ...prev,
              {
                message: value,
                time: dateTimeParser(),
                type: "success",
              },
            ]);
            userAgentMutation(res.data.createWebChatWidgetLead.message);
            const element = document.getElementById("message");
            element.scrollTop = element.scrollHeight + 1000;
            props.setactionStarted(true);
            props.setMessage("");
            props.setlead(res.data.createWebChatWidgetLead.lead);
          } else {
            message.error(res.data.createWebChatWidgetLead.message);
          }
        });
      } else {
        message.error("Please enter a message");
      }
    } else {
      // use Socket here for communication
      if (value === "") {
        return message.error("Please enter a message");
      } else {
        props.Socket.emit("receive_webchat_message", {
          message: value,
          lead_id: +props?.lead?.id,
        });

        props?.setMessageList((prev) => [
          ...prev,
          {
            message: value,
            time: dateTimeParser(),
          },
        ]);
        props.messenger.current.scrollIntoView({ behavior: "smooth" });
        props.setactionStarted(true);
        props.setMessage("");
      }
    }
  };

  return (
    <div
      className={
        props.actionStarted && props.flow === "3"
          ? "input-with-sms-container"
          : "input-container"
      }
    >
      {props.actionStarted &&
      props.flow === "3" &&
      props.phoneNumber !== "" &&
      !props.Download ? (
        <div style={{ padding: "0px 10px" }}>
          <p style={{ fontWeight: "bold" }}>
            You can continue to chat from your{" "}
            <span
              style={{ color: "rgb(142, 11, 97)", cursor: "pointer" }}
              onClick={() => window.open(`sms:123456789012`)}
            >
              {" "}
              SMS App
            </span>{" "}
            to this number{" "}
            <span
              style={{ color: "rgb(142, 11, 97)", cursor: "pointer" }}
              onClick={() => window.open(`sms:123456789012`)}
            >
              +1-234567-89012
            </span>
          </p>
        </div>
      ) : (
        <>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <p style={{ fontWeight: "bold" }}>
              {props.actionStarted
                ? props.phoneNumber === "" && props.flow === "1"
                  ? "Enter you Phone Number"
                  : "Enter your message here"
                : "Cannot Find Answer?"}
            </p>
            {props.actionStarted && props.flow === "3" && (
              <p
                style={{
                  cursor: "pointer",
                  color: "#0000ffa3",
                  fontWeight: "bold",
                }}
                onClick={() => {
                  props.setphoneForm((prev) => !prev);
                  props.messenger.current.clientHeight =
                    props.messenger.current.scrollHeight;
                }}
              >
                {props.phoneForm ? "Switch to chat" : "Switch to SMS"}
              </p>
            )}
          </div>
          <div className="input-button-wrapper">
            {" "}
            {props.phoneNumber === "" &&
            props.actionStarted &&
            props.flow === "1" ? (
              <>
                {" "}
                <PhoneInput
                  country={"us"}
                  value={props.PhoneValue}
                  onChange={(phone) => props.setPhoneValue(phone)}
                />
              </>
            ) : (
              <div className="input-switch" style={{ width: "100%" }}>
                <TextArea
                  style={{ marginBottom: "0.5em" }}
                  value={props.message}
                  rows={2}
                  onChange={(e) => props.setMessage(e.target.value)}
                  placeholder={
                    props.actionStarted
                      ? "Enter message..."
                      : "Ask your question here..."
                  }
                  onPressEnter={(e) => {
                    e.preventDefault();
                    sendMessage(props.message);
                  }}
                />
                {props.actionStarted && props.flow === "3" && (
                  <div style={{ width: "100%", textAlign: "center" }}></div>
                )}
              </div>
            )}
            <Button
              onClick={() => {
                sendMessage(props.message);
              }}
              disabled={props.message === ""}
              style={{ borderRadius: "15px", marginLeft: "-8%" }}
              type="primary"
            >
              {" "}
              <SendOutlined />
            </Button>
          </div>
        </>
      )}
    </div>
  );
};
